import { Block } from '@ali/4ever-cangjie';
import createTableRow from './createTableRow';
import Table from '../../mo/models';
import TableRow from '../../mo/models/tableRow';

interface CreateTableParams {
  rowSize: number;
  colSize: number;
  data: Table['data'];
}

export default function createTable(params: CreateTableParams) {
  const { rowSize, colSize, data } = params;
  const nodes: TableRow[] = [];
  for (let i = 0; i < rowSize; i += 1) {
    nodes.push(createTableRow(colSize));
  }
  return Block.create({
    type: 'table',
    data,
    nodes,
  });
}